<template>
    <div id="articleView">
        <div class="content">
            <h2 class="content-title">vue.jsaxiosget,post</h2>
            <div class="content-data">chchyu</div>
            <el-divider></el-divider>
            <div class="content-data">2019-2-12</div>
            <h1>
                <a href="#kjpush" name="kjpush"></a>
                KJPush
            </h1>
            <p>
                KJPushAndroidAndroid
                <br/>
                ()socketKJPushsocket
            </p>
            <h2><a href="#" name=""></a>
                
            </h2>
            <pre><code class="javascript">router.push({ path: 'home' })
const userId = '123'
router.push({ name: 'user', params: { userId }}) // -> /user/123
router.push({ path: `/user/${userId}` }) // -> /user/123
//  params 
router.push({ path: '/user', params: { userId }}) // -> /user
</code></pre>
            <div class="tag">
                <el-tag color="white">javascript</el-tag>
            </div>
        </div>
        <messageBox textTip="!" type="articleReview"/>
        <article-message></article-message>
    </div>
</template>

<script>
    import messageBox from '../message/messageBox.vue';
    import ArticleMessage from "~/component/message/articleMessage";

    export default {
        //
        name: 'articleView',
        components: {
            ArticleMessage,
            messageBox
        },
        data() {
            return {
                id: this.$route.params.id
            };
        }
    };
</script>

<style scoped>
    .content {
        color: #475669;
        border: 1px solid #eaeefb;
        padding: 15px;
        border-radius: 5px;
        box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.1);
    }

    .content-data {
        font-size: 12px;
        text-align: right;
        margin-right: 15px;
    }

    .el-divider {
        margin-bottom: 3px;
        margin-top: 3px;
    }

    pre {
        margin: 15px 0 15px 0;
        background-color: #282c34;
        padding: 15px 15px 15px 30px;
        border-radius: 4px;
        border-left: 6px solid #516363;
        overflow-x: auto;
    }

    code {
        font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
        color: #fff;
        font-size: 0.85em;
        line-height: 1.8;
    }

    .el-tag {
        color: #475669;
        border: 1px solid rgba(0, 0, 0, 0.1);
    }
</style>



